nav {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

// Sliding animations for drawers
.drawerRight {
  position: fixed;
  top: 0;
  right: -80%;
  bottom: 0;
  z-index: 5;
  // min-width: 80%;
  // max-width: 80%;
  // width: 100%;
}
.drawerRightIsActive {
  right: 0;
}

@media screen and (min-width: 769px) {
  .drawerLeft,
  .drawerRight {
    min-width: 300px;
  }
}
// Slide animations
.slide-right-leave-active,
.slide-right-enter-active,
.slide-left-leave-active,
.slide-left-enter-active {
  transition: 0.3s;
}
.slide-right-enter {
  transform: translate(100%, 0);
}
.slide-right-leave-to {
  transform: translate(100%, 0);
}
.slide-left-enter {
  transform: translate(-100%, 0);
}
.slide-left-leave-to {
  transform: translate(-100%, 0);
}
