





































































































































































































































#layout {
  width: 100%;
  min-height: 100%;
}
.gxDesktopTopNav {
  top: 0;
  width: calc(100% - 9.5rem);
}
.gxDesktopTopNav--collapsed {
  top: 0;
  width: calc(100% - 2.75rem);
}
.gxDesktopTopNav--noDrawer {
  width: calc(100%);
}
.gxMoMenu {
  transform: translate(-1rem, -1.8125rem);
}
.gxBottomNav {
  height: 3.375rem;
}
.gx-padding-menu {
  padding-bottom: 3.375rem;
}
