




















































































































































































































































/* stylelint-disable */
.label-mo {
  top: -6px;
}
.label-de {
  font-size: 12px;
}
.gxDropdown {
  min-height: 4.5rem;
  font-size: 1.125rem;
  margin-left: -8px;
}

.gxDropdown .multiselect:focus {
  border-bottom: 1px solid var(--black);
}

.gxAssistive {
  font-size: 0.75rem;
}

select {
  padding-right: 1rem;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
}
select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
}
.placeholder-mo {
  display: none;
}

i {
  position: absolute;
  right: 1rem;
  pointer-events: none;
  cursor: pointer;
}

.innerVal {
  top: 0;
  left: 0;
}

// animated label on focus
.gxDropdownLabel {
  position: absolute;
  top: 1.25rem;
  left: 0;
  font-size: 0.75rem;
  color: var(--gray-500);
  letter-spacing: 0;
  transition-duration: 0.3s;
  transition-property: top, left, font-size;
}

.gxDropdownLabel__mo {
  position: absolute;
  top: 1.25rem;
  left: 0;
  z-index: 49; // Multiselect uses a z-index of 50 on active
  font-size: 1.125rem;
  letter-spacing: 0;
  transition: 0.3s all;
}
.gxDropdownLabel__mo:focus {
  top: 0;
  left: 0;
  font-size: 0.75rem;
  color: var(--primary);
}

.gxDropdownLabel--focus {
  top: 0;
  left: 0;
  z-index: 51; // Multiselect uses a z-index of 50 on active
  font-size: 0.75rem;
}

@screen de {
  .gxSelect {
    font-size: 0.75rem;
  }
  .gxDropdown {
    min-height: 4.125rem;
  }
}
