

































































/* stylelint-disable */
.counter {
  position: relative;
  width: 100%;
  width: 50px;
  // overflow: hidden;
  // overflow: hidden;
}
.scroll-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 240px;
  padding-top: 100px;
  padding-bottom: 100px;
  overflow: auto;
  scroll-behavior: smooth;
}
.scroll-area::-webkit-scrollbar {
  display: none;
}
.number {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  height: 46px;
  line-height: 1;
}

.arrow {
  position: absolute;
  top: calc(50% - 20px);
  display: flex;
  align-items: center;
  height: 46px;
  font-size: 1.5rem;
  color: var(--white);
}
.left {
  left: -12px;
}
.right {
  right: -12px;
}
