  width: 3.125rem;
  height: 3.125rem;
  font-size: 18px;
}
.gxFAB {
  font-size: 1.5rem;
}

.fabLarge {
  width: 3.25rem;
  height: 3.25rem;
  font-size: 1.25rem;
}
.fabMobile {
  font-size: 1.125rem;
}

@screen de {
  .fabLarge {
    width: 3.25rem;
    height: 3.25rem;
    font-size: 1rem;
  }
  .fabMini {
    width: 2.75rem;
    height: 2.75rem;
  }
  .gxFAB {
    font-size: 0.75rem;
  }
}

// Loader
@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spinner::before {
  position: absolute;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  content: '';
  border: 2px solid #a176ff;
  border-top-color: #6d31ff;
  border-radius: 50%;
  animation: spinner 0.6s linear infinite;
}
