
















































































































.options-container {
  top: calc(28px + 4px);
  right: 0;
  border-radius: 8px 0 8px 8px;
  transform-origin: top right;
}
.option {
  height: 1.375rem;
}
.optionBox {
  margin-top: 3px;
}
.optionIcon {
  margin-bottom: 1px;
}
.option:first-child {
  border-radius: 8px 0 0 0;
}
.option:last-child {
  border-radius: 0 0 8px 8px;
}
button:focus {
  outline: none;
}
