





























































































































































































































































































































































































.gxCaretPrimary {
  caret-color: var(--primary);
}
.gxCaretAlert {
  caret-color: var(--alert);
}
.gxLabel {
  padding-bottom: 11px;
}
.gxInput {
  padding-top: 12px;
  padding-bottom: 11px;
}
.gxIcon {
  padding-bottom: 16px;
}
.gxIconPaddingLeftLarge {
  padding-left: 50px;
}

@media screen and (min-width: 768px) {
  .gxIcon {
    padding-bottom: 16px;
  }
  .gxLabel {
    padding-bottom: 13px;
  }
  .gxInput {
    padding-top: 14px;
    padding-bottom: 13px;
  }
  .gxIconPaddingLeftLarge {
    padding-left: 44px;
  }
}
