










































































































































































































































































































































.multiselect__select {
  display: none;
}
.gxMultiselectWrapper.leftIcon .multiselect__single,
.gxMultiselectWrapper.leftIcon .multiselect__placeholder {
  padding-left: 28px;
}
.gxMultiselectWrapper.leftIcon::before {
  position: absolute;
  bottom: 1.75rem;
  z-index: 1;
  font-family: 'Material Icons Round', serif;
  content: 'calendar_today';
}
/* stylelint-disable */
@import '~vue-multiselect/dist/vue-multiselect.min.css';
// Limit overflow on selection
.multiselect__tags .multiselect__single {
  // padding-right: 4px;
  overflow-x: hidden;
  text-overflow: ellipsis;
}
.arrow {
  position: absolute;
  right: 0;
  padding-right: 8px;
  bottom: 8px;
  z-index: 1;
}
.gx-icon-right .arrow {
  bottom: 5px;
}

.gx-icon-right .arrow {
  bottom: 13px;
}

.multiselect__placeholder {
  margin-bottom: 0;
  color: var(--gray-500);
}
.multiselect__content-wrapper {
  position: none;
  border-radius: 0.5rem;
  transform: translateY(4px);
}

.has-options .multiselect__content-wrapper {
  height: 200px;
}
.dropdown .multiselect__placeholder {
  color: var(--white);
}
.dropdown .multiselect__tags {
  border-bottom: 1px solid var(--gray-500);
}
.dropdown .border-with-value .multiselect__tags {
  border-bottom: 1px solid var(--black);
}

.multiselect__tags {
  padding-top: 16px;
  padding-left: 0;
  font-size: 0.75rem;
  border: none;
  border-bottom: 1px solid var(--black);
  border-radius: 0;
}

// .multiselect__tags:hover {
//   background-color: var(--gray-100);
// }

.gx-icon-right .multiselect__tags {
  display: flex;
  align-items: flex-end;
  min-height: 50px;
  padding: 8px 0;
  padding-bottom: 12px;
  font-family: Nunito;
  font-size: 0.75rem;
  color: var(--gray-500);
  background: none;
  border: none;
  border-bottom: 1px solid var(--black);
  border-radius: 0;
}
.gxBorder .multiselect__tags {
  font-family: Nunito;
  font-size: 0.75rem;
  color: var(--gray-500);
  border: none;
  border-bottom: 1px solid var(--primary);
  border-radius: 0;
}
.error .multiselect__tags {
  border-bottom: 1px solid var(--alert);
}
.error .multiselect__placeholder {
  color: var(--alert);
}

.error .typo__label {
  color: var(--alert);
}
.error .multiselect__single {
  color: var(--alert);
}
.dropdown .error .multiselect__placeholder {
  display: none;
}

.multiselect__element,
.multiselect__option {
  // .multiselect__option targets the li when no results are found "No encontramos tu búsqueda"
  height: 29px;
  font-size: 0.75rem;
  color: rgba(25, 27, 29, 1);
}
.multiselect__option--selected {
  height: 29px;
  color: var(--black);
  background: white;
}
.multiselect__option--selected::after {
  height: 29px;
  font-family: 'Material Icons Round', serif;
  color: var(--black);
  content: 'check';
  background-color: transparent;
  transform: translateY(-5px);
}
.multiselect,
.multiselect__input,
.multiselect__single {
  padding: 0;
  padding-left: 8px;
  margin: 0;
  // font-family: Gothic A1;
  // font-size: 1rem;
  // color: var(--black);
}
.gxMultiselectWrapper input,
.gxMultiselectWrapper input::-webkit-input-placeholder {
  font-family: Nunito;
  font-size: 0.75rem;
  font-weight: 300;
  // line-height: 1rem;
}
.multiselect__single {
  margin-bottom: 0;
  font-size: 0.75rem;
  color: rgba(25, 27, 29, 1);
}
.multiselect__option.multiselect__option--highlight {
  color: rgba(25, 27, 29, 1);
  vertical-align: middle;
  background-color: var(--secondary-50);
}
.multiselect__option.multiselect__option--highlight::after {
  background: transparent;
}
.multiselect__option {
  min-height: 29px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 8px;
}
.multiselect__tag {
  margin-left: 16px;
  // line-height: 20px;
  background-color: var(--primary);
}
.multiselect__tag-icon {
  background-color: var(--primary);
}
.multiselect__tag-icon:hover,
.multiselect__tag-icon:focus {
  background-color: var(--secondary-300);
}
.multiselect__tag-icon::after {
  color: var(--white);
}
// Taggable overrides
.gx-taggable .multiselect__tags {
  min-height: 34px;
  padding-top: 0;
  padding-left: 4px;
  border: 1px solid var(--black);
  border-radius: 5px;
}
.gx-taggable .typo__label {
  margin-top: 10px;
  margin-bottom: 8px;
  // line-height: 28px;
}
.gx-taggable .multiselect__tag {
  display: inline-flex;
  align-items: center;
  min-width: 74px;
  height: 26px;
  margin: 0;
  margin-top: 4px;
  margin-right: 4px;
  font-family: 'Nunito';
  font-size: 0.625rem;
  color: var(--secondary-300);
  text-align: center;
  letter-spacing: 0.24px;
  background-color: var(--secondary-50);
  border-radius: 9999px;
}
.multiselect__tag > span {
  margin: 0 auto;
}
.gx-taggable .multiselect__tag-icon {
  display: flex;
  align-items: center;
  padding-right: 16px;
  padding-left: 16px;
  margin: 0;
  color: var(--secondary-300);
  background-color: var(--secondary-50);
  transform: translateX(5px);
}
.gx-taggable .multiselect__tag-icon::after {
  font-size: 0.625rem;
  color: var(--secondary-300);
  background-color: var(--secondary-50);
  transform: translateX(-4px);
}
.gx-taggable .arrow {
  position: absolute;
  top: 30%;
  right: 8px;
  color: var(--black);
}
.gx-taggable .multiselect__placeholder {
  margin-top: 6px;
}
.gx-taggable .multiselect__input::placeholder {
  color: white;
}
.dropdown .multiselect__input::placeholder {
  color: white;
}
// Icon Right WITH CHEVRON
.gx-icon-right .typo__label {
  position: absolute;
  // transform: translateY(5px);
}
.gx-icon-right .typo__label:focus {
  color: var(--primary);
}

/* START - Animate label on top of placeholder when using the caret-right prop */
// Hide placeholder and work animations with the label
.gx-icon-right .multiselect__input::placeholder {
  color: white;
}
.gx-icon-right .multiselect__placeholder {
  display: none;
}
// On focus, animate label to new position and change text color
.gx-icon-right .typo__label {
  top: 1.25rem;
  font-size: 0.75rem;
  letter-spacing: 0;
  transition: 0.3s all;
}
.gx-icon-right .typo__label--focus {
  top: 0;
  z-index: 51; // Multiselect uses a z-index of 50 on active
  font-size: 0.625rem;
}

.gx-icon-right .typo__label--blur {
  top: 0;
  font-size: 0.625rem;
}

/* END - Animate label on top of placeholder when using the caret-right prop */
.gx-icon-right .multiselect:focus {
  border-bottom: 1px solid var(--primary);
}

/* scrollbar */

/*  width */
::-webkit-scrollbar {
  width: 6px;

  /* horizontal scrollbar width */
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--gray-400);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--gray-500);
}

@media only screen and (max-width: 800px) {
  .multiselect__input {
    font-size: 1.125rem;
  }
  .typo__label {
    z-index: 1;
    height: 12px;
    font-size: 12px;
  }
  .arrow {
    font-size: 1.125rem;
  }

  .gx-icon-right .arrow {
    bottom: 3px;
  }
  .gx-icon-right .multiselect__tags {
    padding-bottom: 8px;
  }
  .multiselect__tags {
    font-size: 1.125rem;
  }
  .gx-taggable .multiselect__tags {
    min-height: 48px;
    padding-left: 8px;
  }
  .gx-taggable .multiselect__tag {
    min-width: 88px;
    height: 32px;
    padding: 6px 26px 6px 16px;
    margin-top: 8px;
    margin-right: 4px;
    font-size: 1rem;
  }
  .gx-taggable .multiselect__tag-icon::after {
    font-size: 1rem;
  }
  .gx-taggable .multiselect__tag-icon::after {
    font-size: 0.875rem;
    transform: translate(-8px, -1px);
  }
  .error .typo__label {
    color: var(--alert);
  }
  .gx-icon-right .typo__label {
    font-size: 1.125rem;
  }
  .gx-icon-right .typo__label--focus {
    font-size: 0.875rem;
  }
  .error {
    font-size: 12px;
  }
  .multiselect__single {
    overflow: hidden;
    font-size: 1.125rem;
    // Avoid double line
    line-height: inherit;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .multiselect__placeholder {
    font-family: Nunito;
    font-size: 1.125rem;
    font-weight: 300;
    // line-height: 1rem;
    color: var(--gray-500);
  }
  .gx-icon-right .typo__label {
    position: absolute;
    z-index: 1;
    transform: translateY(-3px);
  }
  .gx-icon-right .typo__label--blur {
    font-size: 0.875rem;
  }
  .gxMultiselectWrapper input,
  .gxMultiselectWrapper input::-webkit-input-placeholder {
    font-size: 1.125rem;
  }
}
.gx-rightIcon {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 50px;
}
.gx-rightIcon--de {
  padding-right: 32px;
  // padding-left: 32px;
}
.gx-rightIcon--mo {
  padding-right: 16px;
  // padding-left: 16px;
}

.gx-icon-left .multiselect__tags {
  padding-left: 24px !important;
  margin-left: -10px;
}
