.gxCardImage {
  max-height: 200px;
}
.grow-enter-active {
  animation: grow 0.5s;
}
.grow-leave-active {
  animation: grow 0.5s reverse;
}

@keyframes grow {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
