













































































































/* stylelint-disable */
label {
  display: inline-block;
  line-height: 1rem;
}
input {
  width: 0;
  height: 0;
  opacity: 0;
}
input + span {
  top: 0;
  right: 0;
  left: 0;
  line-height: 1rem;
}
.label-bottom {
  margin: 0 auto;
}
.label-inline {
  margin: 0;
}
input + span::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 1rem;
  height: 1rem;
  font-size: 1rem;
  line-height: 1rem;
  color: var(--gray-500);
  content: 'check_box_outline_blank';
  border: none;
  opacity: 1;
}
input + span::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 0.5rem;
  height: 0.5rem;
  font-size: 1rem;
  line-height: 1rem;
  color: var(--primary-500);
  content: 'check_box';
  border: none;
  opacity: 0;
  transition: opacity 0.2s ease 0s;
}
input:checked + span::after {
  opacity: 1;
}
input:disabled + span::before,
input:disabled:checked + span::after {
  color: var(--gray-300);
  opacity: 0.7;
}
