









































































.gxChipiconLeft {
  position: relative;
  top: 1px;
  // Compensate for trim area in material icons
  left: -3px;
  font-size: 1rem;
}
.gxChipiconRight {
  position: relative;
  top: 1px;
  // Compensate for trim area in material icons
  right: -3px;
  font-size: 1rem;
}

.gxChipContainer {
  min-height: 2rem;
}

.non-clickable {
  min-width: 88px;
}

@screen de {
  .gxChipiconLeft {
    top: -1px;
    font-size: 0.625rem;
  }
  .gxChipiconRight {
    top: -1px;
    font-size: 0.625rem;
  }
  .gxChipText {
    position: relative;
    // top: -2px;
    padding-top: 0;
    font-size: 0.625rem;
  }
  .gxChipContainer {
    height: 1.625rem;
    margin-bottom: 0.25rem;
  }
}
